.logo-name {
  height: 60px;
  margin-right: 70px;
}

.nav-container {
  width: 85%;
  position: absolute;
  z-index: 20;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  top: 35px;
}

.nav-container .h-navbar {
  width: 100%;
}

.nav-container .h-navbar .nav-link {
  padding-top: 13px;
  padding-right: 35px;
  padding-left: 35px;
  float: none;
  font-family: Montserrat, sans-serif;
  color: white;
  font-size: 13px;
  letter-spacing: -1px;
  text-transform: uppercase;
  text-decoration: none;
}
